body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
    display: flex;
}

.item {
    flex: 1 1;
    padding: 2%;
    position: relative;
    overflow: hidden;
}
.overlay {
    transition: bottom ease 1s;
    position: absolute;
    bottom: -100px;
    left: 0;
    width: 100%;
    opacity: .7;
    background-color: antiquewhite;
  
}

.item:hover .overlay {
    opacity: 0.6;
    bottom: 0;
}

@media only screen and (max-width: 400px) {
    .container {
        flex-direction: column;
    }
}

.textbox {
    flex: 2 1;
}
.aside-1 {
    flex: 1 1;
}

.aside-2 {
    flex: 1 1;
}
.navigation-link {
  display: grid;
  grid-template-columns: reapeat(auto-fit, minmax(350px,1fr));
  justify-content: right;
  margin: 10px;
  padding: 3%;
  height: 70px;
}

.App {
  text-align: center;
  background-color: bisque;
}

body{
  background-color: bisque
}

img{
  width: 100%;
  
} 


